<template>
  <div class="personal">
    <el-tabs tab-position="left" type="border-card">
    <el-tab-pane label="账户安全">
      <h1>账户安全</h1>
      <h2>个人信息</h2>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="邮箱">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">修改</el-button>
        </el-form-item>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="我的收藏">
      <h1>我的收藏</h1>
      <div class="collection-container">
        <div v-for="item in collectionList" class="collection-block" :key="item.title">
          <img :src="item.imagePath" alt="" width="240px" height="300px">
          <div class="title"><router-link :to="'/abstract/' + item.title">{{item.title}}</router-link></div>
        </div>
      </div>
    </el-tab-pane>
  </el-tabs>
  </div>
</template>
<script>
export default {
  data () {
    return {
      username: '',
      form: {
        email: ''
      },
      collectionList: []
    }
  },
  mounted () {
    this.getUserInfo()
    this.getCollection()
  },
  methods: {
    getUserInfo () {
      this.username = window.sessionStorage.getItem('username')
      this.$keycloak.loadUserProfile().then((data) => {
        console.log(data)
        this.form.email = data.email
      })
      // this.$http.get('/user/' + this.username).then(res => {
      //   this.form.phone = res.data.data
      // })
    },
    getCollection () {
      this.$http.get('/collect/' + this.username + '/collections').then(res => {
        this.collectionList = res.data.data
      })
    },
    onSubmit () {
      this.$http.post('/user/' + this.username, this.form).then(res => {
        const json = res.data
        if (json.code === 200) {
          this.$message.success('修改成功')
        } else {
          this.$message.error('修改失败')
        }
      })
    }
  }
}
</script>
<style scoped>
.el-tabs{
  height: 500px;
}
.collection-container{
  display: flex;
  flex-direction: row;
}
.collection-block{
  width: 250px;
  height: 400px;
  margin-right: 20px;
}
</style>
